/*
We use the react-splitter-layout component. Info about the CSS rules can be found 
at the following links:
    https://www.npmjs.com/package/react-splitter-layout
    https://zesik.com/react-splitter-layout/#/iframe
    https://github.com/zesik/react-splitter-layout/blob/HEAD/src/stylesheets/index.css
*/

.clipply-splitter-layout > .layout-splitter {
    flex: 0 0 auto;
    width: 4px;
    height: 100%;
    cursor: col-resize;
    background-color: rgba(0,0,0,0.1);
}

.clipply-splitter-layout .layout-splitter:hover {
    background-color: rgba(0, 0, 0,0.6);
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.clipply-splitter-layout{
    position:absolute;
    padding-top:40px;
    top:0;   
    height:100%;       
}
.Typist .Cursor--blinking {
    display: inline-block;    
    opacity: 0.5;
    -webkit-animation: blink 0.85s step-start infinite;
            animation: blink 0.85s step-start infinite;   
  }
  @-webkit-keyframes blink {
    0% { visibility: hidden; }
    50% { visibility: visible; }
    100% { visibility: hidden;}
  }
  @keyframes blink {
    0% { visibility: hidden; }
    50% { visibility: visible; }
    100% { visibility: hidden;}
  }
